.search {
  width: 71%;
  margin: 4rem 0 2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #a7c9eb;
  -webkit-box-shadow: 5px 5px 7px #42505c, -5px -5px 7px #405158;
  box-shadow: 5px 5px 7px #152341, -5px -5px 7px #3c454b;
}

.search input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  padding-right: 1rem;

  outline: none;
  color: #a1a1a1;
  background: #a7c9eb;
}

.search:hover {
    transform: scale(1.2, 1.15);

}
.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
  
}

